'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';
import LoginModal from '@/app/components/LoginModal.jsx';
import GaleriaAdopciones from '@/app/components/GaleriaAdopciones.jsx';
import VistaTienda from '@/app/components/VistaTienda.jsx';

export default function HomePage() {
    const { currentUser } = useAuth();
    const [isLoginModalOpen, setLoginModalOpen] = useState(false);

    const [isClient, setIsClient] = useState(false);
    useEffect(() => {
        setIsClient(true);
    }, []);


    if (!isClient) {
        return null;
    }

    if (!currentUser) {
        return (
            <>
                <div 
                    className="relative min-h-screen flex items-center justify-center bg-cover bg-center"
                    style={{ backgroundImage: "url('/patron1.jpg')" }}
                >
                    <div className="absolute inset-0 bg-black opacity-30"></div>
                    <div className="relative z-10 bg-white bg-opacity-80 p-8 md:p-12 rounded-xl shadow-2xl max-w-lg w-full text-center backdrop-blur-sm">
                        <div className="flex justify-center mb-6">
                            <Image src="/LOGO.svg" alt="Magalí Martin Veterinaria" width={250} height={100} />
                        </div>
                        <h1 className="text-2xl md:text-3xl font-bold text-gray-800 mb-4">
                            ¡Bienvenido a la <span className="text-cyan-600">Veterinaria Online</span>!
                        </h1>
                        <p className="text-gray-600 mb-3">
                            Aquí puedes encontrar la mejor atención para tus mascotas.
                        </p>
                        <p className="text-gray-600 mb-3">
                            Reservar turnos para la veterinaria y la peluquería de tus mascotas.
                        </p>
                        <p className="text-gray-600 mb-8">
                            Realizar compras y ver mascotas en adopción.
                        </p>
                        <button
                            onClick={() => setLoginModalOpen(true)}
                            className="w-full bg-cyan-600 hover:bg-cyan-700 text-white font-bold py-3 px-6 rounded-lg text-lg transition-transform transform hover:scale-105 shadow-lg"
                        >
                            Ingresar
                        </button>
                    </div>
                </div>
                <LoginModal 
                    isOpen={isLoginModalOpen} 
                    onClose={() => setLoginModalOpen(false)}
                    useAuthHook={useAuth} 
                />
            </>
        );
    }

    return (
        <main className="flex-grow bg-white">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12 md:py-20">
                <section className="text-center mb-20 md:mb-28">
                    <div className="flex flex-col md:flex-row justify-center items-center gap-8">
                        <Link 
                            href="/turnos/consulta" 
                            className="w-full md:w-auto bg-violet-600 hover:bg-violet-700 text-white font-bold py-5 px-10 rounded-xl text-lg uppercase tracking-wider transition-transform transform hover:scale-105 shadow-xl hover:shadow-2xl focus:outline-none focus:ring-4 focus:ring-violet-300"
                        >
                            Pedir Turno Consulta
                        </Link>
                        <Link 
                            href="/turnos/peluqueria" 
                            className="w-full md:w-auto bg-teal-500 hover:bg-teal-600 text-white font-bold py-5 px-10 rounded-xl text-lg uppercase tracking-wider transition-transform transform hover:scale-105 shadow-xl hover:shadow-2xl focus:outline-none focus:ring-4 focus:ring-teal-300"
                        >
                            Pedir Turno Peluquería
                        </Link>
                    </div>
                </section>
                <GaleriaAdopciones />
                <VistaTienda />
            </div>
        </main>
    );
}
